<template>
<div> 
  <ymt-calendar :initData="initData" @click="handleClickDay" @scroll="handleScroll"></ymt-calendar>

   

</div> 
</template>

<script> 
export default { 
	data(){
		return {
			rawHtml:`<ymt-input 
      title="用户名" 
      placeholder="请录入用户名"  
      @change="handleChange"></ymt-input>`,
      initData:{
            beginDate: '2017-05-01',
            endDate: '2017-12-01',
            lastestSetTime: '2017-08-01',
            policyDateList: [
              {policyDate:'2017-05-03',policyStatus:'2'},
              {policyDate:'2017-06-01',policyStatus:'1'},
              {policyDate:'2017-08-01',policyStatus:'1'},
              {policyDate:'2017-08-02',policyStatus:'1'}
            ]
      }
		}
	},
    methods:{
      handleLeftClick() {
        console.log('handleLeftClick');
        this.$router.back();
      }, 

    	handleClickDay({dayItem,calendar}) {
        if (dayItem.changeStatus == 0){
            calendar.selecting(dayItem);
        }
        else if (dayItem.changeStatus == 1) {
            calendar.unSelecting(dayItem);
        }
        else if (dayItem.changeStatus == 2) {
            calendar.canceling(dayItem);
        }
        else if (dayItem.changeStatus == 3) {
            calendar.unCanceling(dayItem);
        }        
      },
      handleScroll(month) {
        console.log(month,'==========');
      }
    }
}
</script>

<style lang="less" scoped> 
	.top {
    position: relative;
  }  
</style>



